import React, { useEffect } from 'react';
import './carousel.css';

export default function Carousel(props) {
    
    var counter = 1;
    const sliderRef = React.createRef();
    var images;
    var size;

    useEffect(() => {
        images = document.querySelectorAll("#" + props.id + "  .carousel-slider img");
        images[images.length - 1].id = props.id + " first-clone";
        images[0].id = props.id + " last-clone";
        size = sliderRef.current.clientWidth;
        sliderRef.current.style.transform = 'translateX(' + (-size * counter) + 'px)';
    }, []);

    const Next = () => {
        if(counter >= images.length - 1) return;
        sliderRef.current.style.transition = "transform .3s ease-in-out";
        counter++;
        updateDots();
        sliderRef.current.style.transform = 'translateX(' + (-size * counter) + 'px)';
    }

    const Previous = () => {
        if(counter <= 0) return;
        sliderRef.current.style.transition = "transform .3s ease-in-out";
        counter--;
        updateDots();
        sliderRef.current.style.transform = 'translateX(' + (-size * counter) + 'px)';
    }

    const MoveTo = (e) => {
        sliderRef.current.style.transition = "transform .3s ease-in-out";
        counter = parseInt(e.target.id) + 1;
        updateDots();
        sliderRef.current.style.transform = 'translateX(' + (-size * counter) + 'px)';
    }

    const updateDots = () => {
        document.querySelector("#" + props.id + " .current").classList.remove("current");
        if(images[counter].id === props.id + " last-clone" || images[counter].id === props.id + " first-clone") {
            var dots = document.querySelectorAll("#" + props.id + " .carousel-dot");
            if(images[counter].id === props.id + " last-clone") {
                dots[dots.length - 1].classList.add("current");
            } else {
                dots[0].classList.add("current");
            }
            return;
        }
        document.querySelectorAll("#" + props.id + " .carousel-dot")[counter-1].classList.add("current");
    }

    const loop = () => {
        sliderRef.current.style.transition = "none";
        if(images[counter].id === props.id + " last-clone") {   
            counter = images.length - 2;    
        }
        if(images[counter].id === props.id + " first-clone") {
            counter = images.length - counter;
        }
        sliderRef.current.style.transform = 'translateX(' + (-size * counter) + 'px)';
    }
    
    return (
        <div id={props.id} className="carousel-container">
            <Slider ref={sliderRef} onTransitionEnd={() => loop()}>
                {props.images.map((item, key) => {
                    console.log(item.default);
                    return <img key={key} src={item.default} alt={item} />
                })}
            </Slider>
            <ul className="carousel-dots">
                {props.images.map((item, key) => {
                    return <li key={key} id={key} className={`carousel-dot ${key === 0 ? "current" : ""}`} onClick={(e) => MoveTo(e)} />
                })}
            </ul>

            <CarouselBtn id="prev" onClick={() => Previous()} icon="fas fa-angle-left" />
            <CarouselBtn id="next" onClick={() => Next()} icon="fas fa-angle-right" />
        </div>
    );
}

const Slider = React.forwardRef((props, ref) => {
    return (
        <div ref={ref} className="carousel-slider" onTransitionEnd={props.onTransitionEnd}>  
            {props.children[props.children.length - 1]}
            {props.children}
            {props.children[0]}
        </div>
    );
});

function CarouselBtn(props) {
    return (
        <div className="carousel-button-overlay" id={props.id} onClick={props.onClick}>
            <div className="carousel-button-holder">
                <i className={props.icon}/>
            </div>
        </div>
    );
}